import "core-js/modules/es.array.push.js";
import Header from '@/components/Header.vue';
import { onMounted, toRefs, reactive } from 'vue';
import { useStore } from 'vuex';
import { showDialog } from 'vant';
import { useRouter, useRoute } from 'vue-router';
export default {
  components: {
    Header
  },
  setup() {
    let data = reactive({
      name: "橘子味柠檬汽水",
      tel: '18758199674',
      totalPrice: 0
    });
    let store = useStore();
    let router = useRouter();
    let route = useRoute();
    // 初始化总价
    const initPrice = () => {
      let price = 0;
      if (store.state.orderList.length) {
        store.state.orderList.forEach(item => {
          price += item.num * item.price;
        });
      }
      data.totalPrice = price;
    };
    // 初始化地址
    const initAdress = () => {
      store.state.userAddress.forEach(item => {
        if (item.isDefault) {
          data.name = item.name;
          data.tel = item.tel;
        }
      });
    };
    onMounted(() => {
      initPrice();
      initAdress();
    });
    // 点击生成订单
    const handelOrder = () => {
      showDialog({
        title: '提示',
        message: '恭喜！您的订单已完成'
      }).then(() => {
        // 更新购物车列表
        // console.log(route)
        // route传参过来的list数组元素值是字符串，所以要加''转字符串进行比较
        let newList = store.state.cartList.filter(item => {
          return !route.query.list.includes(item.id + '');
        });
        store.commit('deleteCartList', newList);
        store.commit('upDateOrderListAll');
        // 点击确定，跳转到订单页面
        router.push('/order');
      });
    };
    const onEdit = () => {
      router.push('/address');
    };
    return {
      ...toRefs(data),
      store,
      onEdit,
      handelOrder
    };
  }
};